.friendList{
    text-align: center;
    width: 320px;
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;  
    padding: 5px;
    background-color: white;  
    
}

.item{
    display: flex;
    align-items: center;
    border: 1px solid rgb(200, 200, 206);    
    padding: 10px;
    margin: 10px;
    margin-bottom: 20px;
    box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
}

.status{
    display: flex;
    width: 15px;
    height: 15px;
    margin: 0 15px;
    border-radius: 50%;
}

.avatar{
    width: 50px;
    margin: 0 15px;
}

.name{
    margin: 0 15px;
font-size: 20px;
    text-align: center;
}


